.emailContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(10, 10, 10, 0.95);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}

.emailContainer > .innerForm {
  width: 400px;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 50px;
  z-index: 200;
}

.emailContainer > .innerForm > form {
  margin-top: 100px;
  display: flex;
  flex-direction: column;

  width: 70%;
  gap: 1rem;
}
.emailContainer > .innerForm > form > .formInformation {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.emailContainer > .innerForm > form > .formInformation > .inputContainer {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}
.emailContainer
  > .innerForm
  > form
  > .formInformation
  > .inputContainer
  > label {
  font-size: 1rem;
}
.emailContainer
  > .innerForm
  > form
  > .formInformation
  > .inputContainer
  > input {
  height: 30px;
  font-size: 1.2rem;
  border-radius: 10px;
  font-family: sans-serif;
  padding-left: 10px;
  font-size: 1rem;
}
.emailContainer
  > .innerForm
  > form
  > .formInformation
  > .inputContainer
  > textarea {
  height: 100px;
  padding: 10px;
  resize: none;
  border-radius: 10px;

  font-family: sans-serif;
}

.submitBtn {
  background-color: rgb(5, 5, 5);
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  border: 1px solid white;
  cursor: pointer;
}

.submitBtn > p {
  color: rgb(243, 236, 236);
}

.closeBtn {
  position: fixed;
  top: 2px;
  right: 3px;
  width: 30px;
  height: 30px;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  opacity: 50%;
  cursor: pointer;
  transition: 0.5s ease;
}
.closeBtn > p {
  font-family: sans-serif;
  font-weight: 800;
  padding: 5px;
}

.closeBtn:hover {
  opacity: 1;
}

@media only screen and (min-width: 750px) {
  .emailContainer > .innerForm {
    width: 600px;
    height: 800px;
  }

  form {
    width: 90%;
    gap: 1rem;
  }

  .emailContainer > .innerForm > form > .formInformation > .inputContainer {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  .emailContainer
    > .innerForm
    > form
    > .formInformation
    > .inputContainer
    > label {
    font-size: 1.2rem;
  }
  .emailContainer
    > .innerForm
    > form
    > .formInformation
    > .inputContainer
    > input {
    height: 40px;
    font-size: 1.2rem;
    border-radius: 10px;
    font-family: sans-serif;
    padding-left: 10px;
    font-size: 1rem;
  }
  .emailContainer
    > .innerForm
    > form
    > .formInformation
    > .inputContainer
    > textarea {
    height: 100px;
    padding: 10px;
    resize: none;
    border-radius: 10px;

    font-family: sans-serif;
  }

  .emailContainer > .innerForm > form > .submitBtn {
    width: 200px;
    height: 80px;
  }

  .emailContainer > .innerForm > form > .submitBtn > p {
    font-size: 2rem;
  }
  .submitBtn > p > svg {
    font-size: 2rem;
  }

  .closeBtn {
    width: 50px;
    height: 50px;
    background-color: white;
    top: 30px;
    right: 30px;
  }
  .closeBtn > p {
    font-size: 2rem;
    color: black;
  }
  label {
    font-size: 2rem;
  }
}
