.skillcontainer {
  width: 100%;
  height: 600dvh;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.skilltitleicon {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: start;
  position: sticky;
  top: 0;
  height: 30vh;
  width: 100%;
  z-index: 10;
}

.skilltitleicon > p {
  font-size: 2.5rem;
}

.skillprogressbar {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.skillprogressbar > .flipContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  perspective: 1000px;
}

.skillprogressbar > .flipContainer > .flipInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: 80%;
  height: 350px;

  border-radius: 10px;
  transition: transform 0.5s;
  transform-style: preserve-3d;

  position: relative;
}

.skillsRateTitle > h1 {
  font-size: 1.2rem;
  text-align: center;
}

.skillsRateTitle {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.skillsRateTitle > p {
  font-size: 0.9rem;
  font-family: sans-serif;
  text-align: justify;
  word-spacing: -1px;
}

.skillprogressbar > .flipContainer > .flipInner > .fronPage,
.skillprogressbar > .flipContainer > .flipInner > .flipBack {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.skillprogressbar > .flipContainer > .flipInner > .flipBack {
  background-color: #4e0a0a;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
}

.skillprogressbar > .flipContainer > .flipInner > .flipBack > p {
  text-align: justify;
  padding: 10px;
  font-family: sans-serif;
}

@media only screen and (min-width: 750px) {
  .skillsRateTitle > p {
    font-size: 1.2rem;
    width: 600px;
    margin: 0 auto;
  }

  .skilltitleicon > h1 {
    font-size: 4rem;
  }
  .skillsRateTitle > h1 {
    font-size: 2rem;
  }

  .skillprogressbar > .flipContainer > .flipInner {
    width: 400px;
  }
}
