.app-container {
  min-width: 100%;
  min-height: 100%;
  scroll-behavior: smooth;
  scroll-snap-type: mandatory;
  isolation: isolate;
  position: relative;
}

.bgVideo {
  min-height: 100%;
  min-width: 100%;
  position: fixed;
  inset: 0;
  z-index: -1;
}
video {
  min-height: 100%;
  min-width: 100%;

  object-fit: cover;
}

.bgVideo::after {
  content: "";
  position: absolute;
  background-color: rgb(0, 0, 0, 0.9);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
}

.sectionWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 20px;
  z-index: 900;
}

@media only screen and (min-width: 1200px) {
  .sectionWrapper {
    margin: 0 auto;
    width: 80%;
  }
}
